import Global from "../Global";
import { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
import EmptyPosts from "./EmptyPosts";

export default function Posts({ username }) {
  const url = Global.url;
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  //fetch data from the API

  const getPosts = async () => {
    const res = await fetch(`${url}/posts`);
    const data = await res.json();
    const myPosts = data.articles.filter((post) => post.author === username);
    setPosts(myPosts);
    setLoading(false);
  };

  const handleDelete = async (id) => {
    try {
      setPosts(posts.filter((post) => post._id !== id));
      await fetch(`${url}/posts/${id}/delete`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    getPosts();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="container-myPosts">
      {loading ? <div className="spinner-border  p-5 spinner-loading" style={{width: "10rem", height: "10rem", fontWeight:"900", margin: "7rem"}} role={"status"}/> : posts.length > 0
        ? posts.map((post, index) => (
            <div
              key={index}
              className="card bg-dark p-4 shadow rounded "
              id="myPost"
            >
              <NavLink
                className="card-header text-white p-4  text-decoration-none border-0 "
                to={`/posts/${post._id}`}
              >
                <h1>{post.title}</h1>
              </NavLink>
              <div className="d-flex gap-3 justify-content-end ">
                <button
                  onClick={() => {
                    handleDelete(post._id);
                  }}
                  className="btn btn-danger x"
                >
                  X
                </button>
                <NavLink to={`/posts/${post._id}/edit`}>
                  <button className="btn btn-warning">Edit</button>
                </NavLink>
              </div>
            </div>
          ))
        : <EmptyPosts/>}
    </div>
  );
}
