import React from "react";
import SignUpForm from "../../components/SignUpForm";
import { useEffect } from "react";
import Header from "../../components/Header";

export default function SignUp() {

  useEffect(() => {
    document.title = "Sign Up";
  }, []);
  

  return (
    <>
      <Header />
      <SignUpForm />
    </>
  );
}
