import React from "react";
import Global from "../Global";
import { useState } from "react";
import { Navigate } from "react-router-dom";

export default function CreatePostForm({ username }) {
  const url = Global.url;

  const [redirect, setRedirect] = useState(false);
  const [loading, setLoading] = useState(false);

  const [article, setArticle] = useState({
    title: "",
    content: "",
    likes: 0,
  });

  //referencia de los formulario

  let titleRef = React.createRef();
  let contentRef = React.createRef();

  const changeState = () => {
    setArticle({
      title:  titleRef.current.value  ,
      content:  contentRef.current.value ,
      author: username,
      likes: 0,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    changeState();
    //preguntar si esta completo
    if (article.title === "" || article.content === " ") {
      alert("Todos los campos son obligatorios");
      return;
    }
    //fetch await POST
    setLoading(true);
    const res = await fetch(`${url}/posts/new`, {
      method: "POST",
      body: JSON.stringify(article),
      headers: {
        "Content-Type": "application/json",
      },
    });
     await res.json();
    setLoading(false);
    setRedirect(true);

  };

  if (redirect) {
    return <Navigate to="/" />;
  }

  return (
    <>
      <div className="createForm ">
        <div
          className="card bg-dark p-5 pb-4 shadow border border-secondary"
          style={{ minWidth: "390px", width: "35vw", maxWidth: "100vw" }}
        >
          <div className="card-header text-white">
            <h1>Create Post</h1>
          </div>

          <div className="card-body">
            <form onSubmit={handleSubmit}>
              <div className="form-group mb-3">
                <label htmlFor="title" className="text-white mb-2">
                  <b>Title:</b>
                </label>
                <input
                  type="text"
                  className="form-control shadow-sm"
                  id="title"
                  placeholder="Enter title"
                  name="title"
                  ref={titleRef}
                  onChange={changeState}
                />
              </div>

              <div className="form-group mb-4">
                <label htmlFor="description" className="text-white mb-2">
                  <b>Content:</b>
                </label>
                <textarea
                  className="form-control z-depth-1 shadow-sm"
                  id="Content"
                  rows="3"
                  placeholder="Enter Content"
                  style={{ height: "35vh" }}
                  name="content"
                  ref={contentRef}
                  onChange={changeState}
                />
              </div>

              <div className="d-flex justify-content-end gap-2">
                {loading ? <div className="btn btn-success"><div className="spinner-border" role={"status"}/></div> :  <button className="btn btn-success">Create</button>}
                
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
